/* ===== Base Styles ===== */
.App {
  text-align: center;
  min-height: 100vh;
  background: #1a1a1a;
  color: white;
  padding: 20px;
  font-family: Arial, sans-serif;
}

/* ===== Header ===== */
.header {
  margin-bottom: 40px;
}

.header h1 {
  color: #61dafb;
  font-size: 2.5em;
  margin-bottom: 30px;
}

/* ===== Search Box ===== */
.search-box {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
}

.hash-input {
  width: 600px;
  padding: 12px 20px;
  font-size: 16px;
  border: 2px solid #61dafb;
  border-radius: 4px;
  background: #2d2d2d;
  color: white;
  outline: none;
  transition: border 0.3s;
}

.hash-input:focus {
  border: 2px solid #4fa8d3;
}

.analyze-btn {
  padding: 12px 25px;
  background: #61dafb;
  color: #000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s, transform 0.2s;
}

.analyze-btn:hover {
  background: #4fa8d3;
  transform: scale(1.05);
}

/* ===== Transaction Details ===== */
.tx-details {
  background: #2d2d2d;
  padding: 25px;
  border-radius: 8px;
  margin: 20px auto;
  width: 80%;
  max-width: 700px;
  text-align: left;
  box-shadow: 0 4px 6px rgba(0,0,0,0.2);
  transition: transform 0.3s;
}

.tx-details:hover {
  transform: scale(1.02);
}

.tx-info div {
  margin: 15px 0;
  padding: 8px 0;
  border-bottom: 1px solid #3d3d3d;
}

.tx-info span {
  color: #61dafb;
  display: inline-block;
  width: 120px;
  font-weight: bold;
}

/* ===== Fraud Risk Card ===== */
.risk-card {
  padding: 20px;
  border-radius: 8px;
  margin: 30px auto;
  width: 80%;
  max-width: 700px;
  border: 2px solid;
  text-align: left;
  transition: transform 0.3s, background 0.3s;
}

/* High-Risk Styling */
.high-risk {
  border-color: red;
  background: rgba(255, 0, 0, 0.1);
  color: red;
}

.low-risk {
  border-color: green;
  background: rgba(0, 255, 0, 0.1);
  color: green;
}

/* Fraud Flags */
.flags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 15px;
}

.risk-flag {
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: 600;
  display: inline-block;
}

/* Flag Colors */
.flag-high { background: red; color: white; }
.flag-medium { background: orange; color: black; }
.flag-low { background: yellow; color: black; }

/* ===== Error Messages ===== */
.error {
  color: #ff4444;
  background: #2d1d1d;
  padding: 15px;
  border-radius: 6px;
  margin: 20px auto;
  width: 80%;
  max-width: 600px;
  border: 1px solid #ff4444;
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .hash-input {
    width: 100%;
  }

  .search-box {
    flex-direction: column;
    align-items: center;
  }

  .tx-details {
    width: 95%;
    padding: 15px;
  }

  .risk-card {
    width: 95%;
  }
}
